export default{
	name: 'bet-info',
	data() {
		return {
			hide: true,
		}
	},
	props: {
		redBall: {
			type: String,
			default: ''
		},
		blueBall: {
			type: String,
			default: ''
		},
		aitem: {
			type: Array,
			default () {
				return []
			}
		},
		current:{
			type: String,
			default: ''
		},
		currentDate:{
			type: String,
			default: ''
		},
		mount:{
			type:String,
			default: ''
		},
		total:{
			type:String,
			default:''
		},
		tooLarge:{
			type:Boolean,
			defalut: false
		}
	},
	methods: {
		toggle() {
			var that = this;
			that.hide = !that.hide;
		},
		random01(){
			this.$emit('clickItem01');
		},
		random05(){
			this.$emit('clickItem05');
		},
		btnSure(){
			this.$emit('clickSure');
		}
	},
} 
